.questions {
  .step {
    &.active {
      display: flex;
    }   
  }

  // radio
  input.form-radio-input {
    &[type="radio"]:checked+label { 
      color: white;
      background-color: $COLOR_CELESTE;
      border-color: $COLOR_CELESTE;

      &:before {
        background-color: $COLOR_BLUE;
        border-color: white;
      }
     } 
  }
  
  .data {
    &__body {
      &.active {
        span { display: none; }

        input {
          width: 100%;
          height: auto;
          opacity: 1;
        }
      }
    }
  }
}