    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?msu51b') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?msu51b') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?msu51b##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
